$primary_color: #007cba;
$secondary_color: #5f5f5f;
$background_color: #dcdcde;
$white_color: #ffffff;

.fb-setting-responsive-control-wrapper {
    .fb-setting-responsive-control {
        margin-bottom: 0;
    }
}


.fb-gallery-wrapper {
    .dnd-items-wrapper {
        list-style: none;
        padding: 0;
        margin: 0;

        li.sortable-item {
            margin-bottom: 10px;
            margin-top: 10px;

            .sortable-header {
                display: flex;
                justify-content: space-between;
                align-items: center;
                background: #f9f9f9;
                border: 1px solid #ddd;
                border-radius: 4px;

                .sortable-title {
                    flex: 12;
                    cursor: pointer;
                    overflow: hidden;
                    padding-left: 5px;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    width: 200px;
                }

                .sortable-sort {
                    border-right: 1px solid #b4b4cb;
                    border-left: 1px solid #b4b4cb;
                    button {
                        &:focus {
                            box-shadow: none;
                            outline: none;
                        }
                    }
                }

                .sortable-delete {
                    button {
                        color: #d63638;
                        &:focus {
                            box-shadow: none;
                            outline: none;
                        }
                    }
                }
            }

            .sortable-item-content {
                padding-top: 10px;
            }
        }
    }
    .sortable-item-content {
        .fb-image-upload-field-wrapper {
            .components-base-control__field {
                position: relative;

                &:hover {
                    .editor-post-featured-image__actions {
                        opacity: 1;
                    }
                }

                .editor-post-featured-image__action {
                    width: 100%;
                    height: unset;
                    padding: 8px 12px;
                    text-align: center;
                    justify-content: center;

                    &.active {
                        background-color: $secondary_color;
                        color: $primary_color;
                        border-radius: 3px;
                    }
                }
            }
        }

    }
}

.fb-feature-icon-group-btn {
    display: flex;
}

button {
    &.components-button {
        &.fb-feature-icon-single-btn {
            align-items: center;
            display: flex;
            flex: 1;
            justify-content: center;
            padding: 6px;
            height: unset;
            line-height: 16px;
            box-shadow: inset 0 0 0 1px #dcdcde;
            color: #5f5f5f;
            margin-bottom: 10px;
            &.active {
                background-color: #dcdcde !important;
                color: #5f5f5f !important;
            }
        }
        &:focus {
            outline: none !important;
            box-shadow: none !important;
        }
    }
}


/* Lightbox Overlay */
.lightbox {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.9);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;

    .lightbox-content {
        .lightbox-close {
            position: absolute;
            top: 30px;
            right: 30px;
            font-size: 28px;
            background: none;
            border: none;
            color: white;
            cursor: pointer;
        }
        .lightbox-counter {
            position: absolute;
            top: 30px;
            left: 30px;
            font-size: 14px;
            color: #fff;
        }
        .lightbox-prev, .lightbox-next {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            font-size: 28px;
            color: white;
            background: none;
            border: none;
            cursor: pointer;
            outline: none;
        }
        .lightbox-prev {
            left: 2%;
        }
        .lightbox-next {
            right: 2%;
        }

        .lightbox-image-container {
            img {
                max-width: 100%;
                max-height: 80vh;
            }
        }
        .lightbox-thumbnails {
            display: flex;
            justify-content: center;
            margin-top: 20px;

            img {
                width: 60px;
                height: 60px;
                object-fit: cover;
                margin: 0 5px;
                cursor: pointer;
                opacity: 0.6;
                transition: opacity 0.3s;

                &.active {
                    opacity: 1;
                    border: 2px solid #fff;
                }
                &:hover {
                    opacity: 1;
                }
            }
        }
    }
}

